<template>
    <el-dialog :title="formTitle" :value="value" v-model="visible" @close="resetFormData">
        <el-form :model="formData" label-width="120px" :rules="formRules" ref="formData">
            <el-form-item label="商品ID" prop="pid" required>
                <el-input v-model.trim.number="formData.pid" auto-complete="off" :disabled="true"
                          v-if="formData.id !=''"></el-input>
                <el-input v-model.trim.number="formData.pid" auto-complete="off" v-else></el-input>
            </el-form-item>

            <el-form-item label="有效时间" required>
                <el-date-picker
                    v-model="formData.startTime"
                    type="datetime"
                    format="yyyy-MM-dd HH:mm:ss"
                    placeholder="选择日期时间">
                </el-date-picker>
                至
                <el-date-picker
                    v-model="formData.endTime"
                    format="yyyy-MM-dd HH:mm:ss"
                    type="datetime"
                    placeholder="选择日期时间">
                </el-date-picker>
            </el-form-item>


        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="visible = false">取消</el-button>
            <el-button size="small" type="primary" @click.native="formSubmit" :loading="formLoading">提交</el-button>
        </div>
    </el-dialog>
</template>

<script type="es6">
    import util, {formatDate} from '../../../api/util'
    import {httpPost} from '../../../api/api';

    export default {
        name: 'vDetail',
        props: ['value', 'data'],
        data() {
            let checkNum = (rule, value, callback) => {
                if (value == 0) {
                    callback(new Error('请输入商品ID'));
                } else {
                    callback();
                }
            };
            return {
                visible: false, //默认隐藏
                showLoading: false,
                formTitle: '新增预售',
                formLoading: false,
                formRules: {
                    pid: [
                        {validator: checkNum, trigger: 'blur'}
                    ],
                },
                formData: {
                    id: '',
                    pid: 0,
                    startTime: '',
                    endTime: '',
                },
                inputVisible: false, //隐藏、显示关键字输入框
                inputValue: '',
                avatarLoading: false,
            }
        },
        computed: {
            detail() { //返回详情
                let _self = this;
                if (!_self.value) { // 弹窗不显示，不进行请求
                    return false;
                }
                if (_self.data.id && _self.visible) {
                    _self.formTitle = '编辑预售';
                    _self.formData = _self.data;
                    if (_self.data.startTime > 0) {
                        _self.formData.startTime = new Date(Number(_self.data.startTime));
                    } else {
                        _self.formData.startTime = '';
                    }
                    if (_self.data.endTime > 0) {
                        _self.formData.endTime = new Date(Number(_self.data.endTime));
                    } else {
                        _self.formData.endTime = '';
                    }
                }
            }
        },
        methods: {
            formSubmit() { //提交表单
                let _self = this;
                if (!_self.formData.startTime || !_self.formData.endTime) {
                    _self.$message.error('有效时间不能为空');
                    return;
                }

                let para = new Object();
                para.id = _self.formData.id;
                para.pid = _self.formData.pid;
                para.startTime = formatDate(_self.formData.startTime, 'yyyy-MM-dd hh:mm:ss');
                para.endTime = formatDate(_self.formData.endTime, 'yyyy-MM-dd hh:mm:ss');

                _self.$refs.formData.validate((valid) => {
                    if (valid) {
                        _self.formLoading = true;
                        httpPost('proPreSave', para, _self, function (res) {
                            _self.formLoading = false;
                            try {
                                _self.$message.success('提交成功');
                                _self.resetFormData();
                                _self.visible = false;
                                _self.$emit('refresh');
                            } catch (error) {
                                util.jsErrNotify(error);
                            }
                        });
                    }
                });
            },
            resetFormData() { //关闭表格弹窗，重置表格数据
                let _self = this;
                _self.formTitle = '新增预售';
                _self.formData = {
                    id: '',
                    pid: '',
                    startTime: '',
                    endTime: '',
                };
                _self.data = {};
            }
        },
        watch: {
            detail(val) { //监测详情变化
            },
            value(val) {
                this.visible = val;
            },
            visible(val) {
                this.$emit('input', val);
            }
        },
        mounted() {
            if (this.value) {
                this.visible = true;
            }
        }
    }
</script>



